<template>
  <div class="root vFlex">
    <div class="hFlex" style="flex: 1; overflow: hidden;">
      <div class="leftMenu vFlex">
        <GhostButton @click="$router.push('/')" />
        <Menu class="menu"
              :default-active="activeMenu"
              @select="handleMenuSelect"
              width="180">
          <MenuItem index="short-key">
            <Icon class="el-icon-mouse"></Icon>
            快捷键
          </MenuItem>
        </Menu>
      </div>

      <div class="content vFlex">
        <div class="row title">{{pageTitle}}</div>
        <div class="row">
          <router-view ref="routerView"></router-view>
        </div>
      </div>
    </div>
    <div class="footer hFlex">
      <div style="flex:1"></div>
      <Button type="primary" size="small" @click="saveSetting">保存</Button>
    </div>
  </div>
</template>

<script type="text/jsx">
import GhostButton from '@/components/GhostButton.vue';

export default {
  name: 'SettingLayout',
  components: {
    GhostButton,
  },
  data() {
    return {};
  },
  computed: {
    activeMenu() {
      const { path } = this.$route;
      let keyName = '';
      if (path.startsWith('/setting/')) {
        keyName = path.replace('/setting/', '');
      }
      return keyName;
    },
    pageTitle() {
      const { path } = this.$route;
      let keyName = '';
      if (path.startsWith('/setting/')) {
        keyName = path.replace('/setting/', '');
      }
      return {
        'short-key': '快捷键设置',
        test: 'Test',
      }[keyName] || '';
    },
  },
  methods: {
    handleMenuSelect(value) {
      if (this.activeMenu !== value) {
        this.$router.push(`/setting/${value}`);
      }
    },
    saveSetting() {
      const { activeMenu } = this;
      const { setting } = this.$store.state;
      if (activeMenu === 'short-key') {
        this.$store.dispatch({
          type: 'setting/setConfig',
          triggerKeys: setting.triggerKeys,
        })
          .then(() => {
            this.$message.success('已保存');
          });
      }
    },
  },
};
</script>

